import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, Button, Div, platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
 
const osname = platform();
 
const Anapa = props =>(
    <Panel id={props.id}>
    <PanelHeader
    left={<HeaderButton onClick = {props.go} data-to = "home">
    {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
    </HeaderButton>}
    >
      Анапа
    </PanelHeader>
    <Group>
    <Div>
        1. Археологический музей «Горгиппия».

         Музей расположен на открытом воздухе на территории археологических раскопок древнего города античных времен.Еще за несколько столетий до нашей эры на этом месте был построен древнегреческий город Синдик. В 4 столетии до нашей эры город стал частью Боспорского царства. Свое название он получил от имени правителя Горгиппы.

Город был, с одной стороны, крепость, защищающая границы государства, с другой стороны, крупный торговый центр. Через него проходили крупные торговые пути древности. Город просуществовал до 240 года до н.э., когда был полностью разрушен варварами. На территории до сих пор ведутся раскопки. Восстановленная часть открыта для посещения.
<br/>
<br/>
Адрес: Краснодарский край, Анапа, ул. Набережная, 4.
<br/>
<br/>
2.Краеведческий музей.

С многовековой историей Анапы знакомит краеведческий музей города. Экспозиция расположена в нескольких залах и охватывает историю с древнейших времен до наших дней. Кроме исторических выставок, здесь представлены этнографические находки исследователей.

Стенды дополняют изделия ремесленников разных эпох, предметы быта, одежда представителей разных национальностей, населяющих территорию. Большой интерес у посетителей вызывает выставка, посвященная местной флоре и фауне.

В 2010 году Анапе присвоено звание «Города военной славы». История города, полностью разрушенного в годы войны и в кратчайшие сроки восстановленного в послевоенные годы, станет экскурсом в героическое прошлое популярного бальнеологического курорта.

Музей располагается в здании бывшей частной гимназии, основанной династией педагогов Щетневых. Музей основали в первые годы советской власти, сначала в форме выставки археологических находок, затем с исторической экспозицией. Здание пережило гражданскую и 2-ю мировую войну.
<br/>
<br/>
Адрес: Краснодарский край, Анапа, ул. Протапова, 1.

</Div>

    </Group>
    </Panel>
);
Anapa.propTypes = {
id: PropTypes.string.isRequired,
go: PropTypes.func.isRequired,
};
 
export default Anapa;