import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, Button, Div, platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
 
const osname = platform();
 
const Piter = props =>(
    <Panel id={props.id}>
    <PanelHeader
    left={<HeaderButton onClick = {props.go} data-to = "home">
    {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
    </HeaderButton>}
    >
      Cанкт-Петербург
    </PanelHeader>
    <Group>
    <Div>
         Город белых ночей – великолепное детище великого императора Петра I, часто называют русской Венецией или Северной Пальмирой. Действительно, сравниться с блистательным Санкт-Петербургом могут лишь немногие города мира. Его неслучайно считают культурной столицей России, ведь буквально на каждом шагу путешественника здесь ждет встреча с удивительным и прекрасным. По количеству достопримечательностей на квадратный километр территории цветущий мегаполис, раскинувшийся на берегах Невы, не уступит популярнейшим туристическим центрам планеты.
<br/>
<br/>
1.Невский проспект.
<br/>
<br/>
Адрес: Станции метро «Адмиралтейская», «Площадь Восстания», «Гостиный двор», «Площадь Александра Невского».
<br/>
<br/>
Невский проспект, простирающийся на 4,5 км от Александро-Невской Лавры до Зимнего дворца,  – это визитная карточка Питера, его душа. Каждое из 240 зданий, фасады которых выходят на знаменитую петровскую першпективу, является архитектурным памятником. Проспект, то сужающийся до 20 м, то расширяющийся до 60 м, пересекает роскошными мостами реки Фонтанка, Мойка, канал Грибоедова. Гуляя по Невскому проспекту, путешественники смогут полюбоваться величественным Казанским собором, колоритными зданиями Гостиного двора и Пассажа, фонтанами на Малой Садовой и в Казанском сквере, памятниками Екатерине II, М. Кутузову, А. Невскому, Б. Де Толли. На Невском всегда многолюдно, ведь знакомство с удивительным Санкт-Петербургом принято начинать именно отсюда.
<br/>
<br/>
2.Петропавловская крепость.
<br/>
<br/>
Адрес: Остров Заячий. Станции метро «Горьковская» или «Спортивная».
<br/>
<br/>
История Санкт-Петербурга начинается, по сути, с закладки в 1703 г. на Заячьем острове Петропавловской крепости, которая надежно защищала город в течение нескольких веков. Цитадель, состоящая из 6 фортов, соединенных равелинами, строилась по плану, разработанному инженером Ламбером и Петром I. По тем временам она считалась практически неприступной. В разные времена в казематах цитадели находились в заточении царевич Алексей, княжна Тараканова, Ф. М. Достоевский, декабристы и народовольцы. Сегодня она превращена в великолепный музейный комплекс. Помимо царской усыпальницы и Петропавловского собора, на территории крепости размещается Музей космонавтики, Кунсткамера, Военно-исторический музей, Монетный двор. По многовековой традиции, не соблюдавшейся лишь в 1934-1957 годах, о наступлении полдня жителей города оповещает залп пушки с Нарышкина бастиона.


</Div>

    </Group>

    </Panel>
);
Piter.propTypes = {
id: PropTypes.string.isRequired,
go: PropTypes.func.isRequired,
};
 
export default Piter;