import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, Button, Div, platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
 
const osname = platform();
 
const Vladivastok = props =>(
    <Panel id={props.id}>
    <PanelHeader
    left={<HeaderButton onClick = {props.go} data-to = "home">
    {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
    </HeaderButton>}
    >
      Владивосток
    </PanelHeader>
    <Group>
    <Div>
    1.Золотой мост.
<br/>
<br/>
Соединение мостом берегов бухты Золотой Рог, стало насущной необходимостью для бурно развивающегося Владивостока, еще в начале прошлого столетия. Вопрос о строительстве инженерного сооружения поднимался и во времена монархии, и в период развитого социализма, однако планам не суждено было осуществиться лишь в нынешнем веке. Реализация грандиозного инфраструктурного проекта, завершившегося торжественным открытием моста 11 августа 2012 г., заняла около 4 лет. Размеры сооружения поражают. Длина моста составляет чуть больше 1388 м при ширине около 33 м. Девять его пролетов удерживаются прочнейшими стальными канатами, проходящими через пилоны, возвышающиеся на 226 м над ростверками. Сооружение способно выдержать землетрясение в 8 баллов и ветер, дующий со скоростью 47 м/с. При всей своей массивности вантовый мост, ставший одним из символов города, выглядит удивительно изящно.
<br/>
<br/>
Адрес: Между Корабельной набережной и портом.
<br/>
<br/>
2.Николаевские триумфальные ворота.
<br/>
<br/>
Потрясающей красоты сооружение является точной копией ворот, возведенных из камня и кирпича в 1891 г., к приезду в город будущего императора Николая II, носившего тогда титул цесаревича. Архитектурный памятник, взорванный большевиками в 1930 г., был реконструирован через 77 лет. Изящное двухъярусное сооружения в пышном русско-византийском стиле с шестигранным конусообразным куполом, шпиль которого увенчан российским гербом, имеет сквозные арочные проходы. Фасад ворот украшают небольшие колонны с изумительными капителями, резные фронтоны. По четырем сторонам второго яруса устроены ниши, напоминающие формой кокошники церкви. В нишу над парадной стороной фасада помещен образ Николая Чудотворца, считающегося главным покровителем моряков, а в три другие – гербы крепости, Приморского края и современного Владивостока.
<br/>
<br/>
Адрес: Адмиральский сквер. Остановка транспорта «Лазо».
<br/>
<br/>
3.Железнодорожный вокзал
<br/>
<br/>
Железнодорожная станция Владивостока стала конечной точкой самого грандиозного инфраструктурного проекта Российской империи – Транссибирской магистрали. Основное здание вокзала было построено в 1893 г. В связи с возросшим объемом пассажирских и грузовых перевозок, в 1912 г. его реконструировали, пристроив порталы. Тогда же здание обрело второй этаж. В экстерьере строения, привлекающего визуальной легкостью композиции, явственного угадываются черты столичного Ярославского вокзала, по образу которого оно возводилось. На перроне вы увидите установленный на гранитный постамент бронзовый километровый столб, увенчанный двуглавым орлом с цифрами 9228. Именно это расстояние преодолевают пассажиры, путешествующие из Владивостока в Москву по Транссибу.
<br/>
<br/>
Адрес: ул. Алеутская, 2. Остановка транспорта «Железнодорожный вокзал».
<br/>
</Div>


    </Group>
    </Panel>
);
Vladivastok.propTypes = {
id: PropTypes.string.isRequired,
go: PropTypes.func.isRequired,
};
 
export default Vladivastok;